import SwBase from '../Base.vue'
export default {
    name: 'SwSteps',
    extends: SwBase,
    data(){
        return {
            currentStep_ : this.currentStep
        }
    },
    computed:{

    },
    methods: {
        handleClick(item,index){
            if(this.readOnly){return}
            this.currentStep_ = index
            this.$emit('click',item,index)
        }
    },
    props: {
        classPrefix: {
            type: String,
            default: 'steps'
        },
        readOnly: {
            type: Boolean,
            default: false
        },
        list: {
            type: Array,
            default: ()=>[]
        },
        currentStep: {
            type: Number,
            default: 0
        }
    }
}



