import SwBase from '../Base.vue'
import classnames from 'classnames'
import { setPhPrefix } from '../../utils/classUtil'
export default {
    name: 'SwGrid',
    extends: SwBase,
    computed: {
        classList(){
            return classnames(this.classObject,this.fluid ? setPhPrefix('grid-fluid'):'')
        }
    },
    props: {
        classPrefix: {
            type: String,
            default: 'grid'
        },
        fluid: String
    }
}



