import SwBase from '../Base.vue'
export default {
    name: 'SwBadge',
    extends: SwBase,
    props: {
        classPrefix: {
            type: String,
            default: 'badge'
        },
        phStyle: {
            type: String,
            default: 'primary'
        },
        classMapping : {
            type: Object,
            default: function(){
                return {
                    'primary':'primary',
                    'info':'info',
                    'success':'success',
                    'error':'error',
                    'warning':'warning',
                    'danger':'danger'
                }
            }
        }
    }
}



