export default {
    name: 'SwCheckbox',
    methods: {
        handleChange(evt){
            this.$emit('change',evt.target.value,evt)
        }
    },
    props: {
        classPrefix: {
            type: String,
            default: 'input'
        },
        label: {
            type: String,
            default: ''
        },
        type: {
            type: String,
            default: 'checkbox'
        },
        disabled: Boolean,
        name: String,
        checked: Boolean,
        value: null
    }
}



