import SwBase from '../Base.vue'
import SwIcon from '../icon/Icon.vue'
export default {
    name: 'SwButton',
    extends: SwBase,
    components: {SwIcon},
    methods: {
        handleClick(evt){
            this.$emit('click',evt)
        }
    },
    props:{
        classPrefix: {
            type: String,
            default: 'button'
        },
        /**
         * 按钮尺寸[sm、md、lg], 默认为sm
         * @property phSize
         * @type String
         * @default 'sm'
         * */
        phSize: {
            type: String,
            default: 'sm'
        },
        phStyle: {
            type: String,
            default: 'primary'
        },
        block: Boolean,
        radius: Boolean,
        hollow: Boolean,
        disabled: Boolean,
        active: Boolean,
        phIcon: String,
        stable: Boolean
    }
}



