import SwBase from '../Base.vue'
export default {
    name: 'SwPopup',
    extends: SwBase,
    model: {
        prop: 'visible',
        event: 'visible-change'
    },
    methods: {
        handleClose(){
            this.$emit('visible-change',false)
            this.closeCallback && this.closeCallback()
        }
    },
    props: {
        classPrefix: {
            type: String,
            default: 'popup'
        },
        align: {
            type: String,
            default: 'top'
        },
        visible: Boolean
    }
}



