import SwBase from '../Base.vue'
export default {
    name: 'SwDialog',
    extends: SwBase,
    model: {
        prop: 'visible',
        event: 'visible-change'
    },
    methods: {
        handleShadowClose(){
            if(!this.shadowDisabled){
                this.handleClose()
            }
        },
        handleClose(){
            this.$emit('visible-change',false)
            this.closeCallback && this.closeCallback()
        }
    },
    props: {
        classPrefix: {
            type: String,
            default: 'dialog'
        },
        visible: {
            type: Boolean,
            default: false
        },
        closeButton: {
            type: Boolean,
            default: false
        },
        shadowDisabled: {
            type: Boolean,
            default: false
        },
        title: String,
        closeCallback: Function
    }
}



