import SwBase from '../Base.vue'
export default {
    name: 'SwTextarea',
    extends: SwBase,
    data(){
        return {
            currentValue : this.value
        }
    },
    computed: {
        inputLength(){
            return this.currentValue ? this.currentValue.length : 0
        }
    },
    methods: {
        handleChange(evt){
            this.currentValue = evt.target.value
            this.$emit('change',this.currentValue,evt)
        }
    },
    props: {
        classPrefix: {
            type: String,
            default: 'textarea'
        },
        value: String,
        count: Boolean,
        maxLength: Number,
        placeholder: String
    }
}



